import { autoinject, customElement, bindable, bindingMode, children, inlineView, useView, containerless, View, DOM } from 'aurelia-framework';
import { UIEvent } from "../../utils/ui-event";
import { UIUtils } from "../../utils/ui-utils";
import * as _ from "lodash";

@autoinject()
@inlineView(`<template class="ui-button \${busy?'ui-busy':''} \${disabled?'ui-disabled':''}" data-value="\${value}" css.bind="{width: width}">
  <a role="button" class="button-el \${theme}" click.trigger="toggleDropdown($event, false)">
    <div class="busy-icon"><ui-glyph glyph="glyph-busy" class="ui-anim-pulse"></ui-glyph></div>
    <div class="button-icon" if.bind="glyph"><ui-glyph glyph.bind="glyph"></ui-glyph></div>
    <div class="button-label"><slot>\${label}</slot></div>
    <div class="button-caret" if.bind="!split && !form && dropdown"><ui-glyph glyph="glyph-chevron-down"></ui-glyph></div>
  </a>
  <a role="button" class="button-el \${splitTheme}" if.bind="split" click.trigger="toggleDropdown($event, true)">
    <div class="button-splitter"></div>
    <div class="button-caret"><ui-glyph glyph="glyph-chevron-down"></ui-glyph></div>
  </a>
</template>`)
@customElement('ui-button')
export class UIButton {
  constructor(public element: Element) {
    if (this.element.hasAttribute('primary')) this.theme = this.splitTheme = 'primary';
    else if (this.element.hasAttribute('secondary')) this.theme = this.splitTheme = 'secondary';
    else if (this.element.hasAttribute('dark')) this.theme = this.splitTheme = 'dark';
    else if (this.element.hasAttribute('info')) this.theme = this.splitTheme = 'info';
    else if (this.element.hasAttribute('danger')) this.theme = this.splitTheme = 'danger';
    else if (this.element.hasAttribute('success')) this.theme = this.splitTheme = 'success';
    else if (this.element.hasAttribute('warning')) this.theme = this.splitTheme = 'warning';

    if (this.element.hasAttribute('icon-top')) this.element.classList.add('icon-top');
    if (this.element.hasAttribute('icon-end')) this.element.classList.add('icon-end');
    if (this.element.hasAttribute('icon-start')) this.element.classList.add('icon-start');
    if (this.element.hasAttribute('icon-hilight')) this.element.classList.add('icon-hilight');

    if (this.element.hasAttribute('xlarge')) this.element.classList.add('size-xl');
    if (this.element.hasAttribute('large')) this.element.classList.add('size-lg');
    if (this.element.hasAttribute('small')) this.element.classList.add('size-sm');

    this.split = this.element.hasAttribute('split');
  }

  bind(bindingContext: Object, overrideContext: Object) {
    if (this.form) this.dropdown = this.form;
  }
  attached() {
  }
  detached() {
    if (this.tether) this.tether.dispose();
    if (this.obMouseup) this.obMouseup.dispose();
    if (this.dropdown) DOM.removeNode(this.dropdown);
  }

  @bindable() glyph = '';
  @bindable() label = '';
  @bindable() value = '';
  @bindable() theme = 'default';
  @bindable() width = 'auto';
  @bindable() splitTheme = 'default';
  @bindable() splitGlyph = 'glyph-caret-down';
  @bindable() dropdown;
  @bindable() form;
  @bindable() busy = false;
  @bindable() disabled = false;

  private tether;
  private obMouseup;

  split = false;
  isDisabled = false;

  disable(b) {
    this.element.classList[(this.isDisabled = (b || this.disabled)) ? 'add' : 'remove']('ui-disabled');
  }

  toggleDropdown(evt, isSplit) {
    if (this.split && !isSplit) return true;
    if (evt.button != 0) return true;
    if (this.dropdown) {
      evt.preventDefault();
      evt.stopPropagation();
      evt.cancelBubble = true;
      if (this.element.classList.contains('ui-open')) {
        UIEvent.fireEvent('menuhide', this.element);
        this.element.classList.remove('ui-open');
        this.dropdown.classList.remove('ui-open');
      }
      else {
        if (UIEvent.fireEvent('menuopen', this.element) !== false) {
          this.element.classList.add('ui-open');
          this.dropdown.classList.add('ui-open');
          if (this.form && this.form.focus) this.form.focus();
          this.tether.position();
        }
      }
      return false;
    }
    return true;
  }
}

@autoinject()
@inlineView(`<template class="ui-button-group \${disabled?'ui-disabled':''}" click.trigger="clickEvent($event)"><slot></slot></template>`)
@customElement('ui-button-group')
export class UIButtonGroup {
  constructor(public element: Element) {
    if (this.element.hasAttribute('vertical')) this.element.classList.add('ui-vertical');
    else this.element.classList.add('ui-horizontal');

    if (this.element.hasAttribute('toggle')) this.element.classList.add('ui-toggle');
  }

  @children('ui-button') buttons = [];
  @bindable({ defaultBindingMode: bindingMode.twoWay }) value = '';
  @bindable() disabled = false;

  buttonsChanged() {
    this.valueChanged(this.value);
  }
  active;
  valueChanged(newValue) {
    if (this.active) this.active.element.classList.remove('ui-active');
    if (this.buttons.length > 0 && (this.active = _.find(this.buttons, (b: any) => b.value === this.value)))
      this.active.element.classList.add('ui-active');
  }

  clickEvent(evt) {
    if (evt.target.dataset['value']) this.value = evt.target.dataset['value'];
  }
}
